* {
    user-select: none;
    margin: 0;
    list-style: none;
    font-size: 1em;
    text-align: center;
}

.navbar {
    display: grid;
    grid-template-columns: 1fr auto auto;
    justify-content: space-between;
    align-items: baseline;
    padding: 0.5rem;
    background-color: #141414;
    border-bottom: 1px solid #e5e5e5;
    margin: auto;
    width: 100%;
    max-height: 8em;
}
.navLink{
    grid-column-start: 1;
}
.logo {
    width: 90px;
    height: 90px;
    margin:auto;
}

.music {
    width: 90px;
    height: 90px;
    margin-right: 1rem;
}

h1 {
    margin: 5px;
    padding: 0.5rem;
    font-size: 1.5rem;
    font-weight: normal;
    color: #050505;
    text-align: center;
}

.h {
    opacity: 0;
}

